.App {
    text-align: center;
    min-height: 400vh;
    background: gainsboro;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}

li {
    float: left;
    display: block;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    color: white;
    cursor: pointer;
}

Link {
    display: block;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

li:hover {
    background-color: darkviolet;
}

li.active {
    background-color: darkviolet;
}

.end {
    min-height: 10vh;
    background-color: gray;
}

.image {
    height: 20px;
    width: 20px;
    float: right;
    margin-right: 40px;
    border: 5px double #B8860B;
    background-color: #B8860B;
}

.image:hover {
    cursor: pointer;
}

.made {
    padding-top: 40px;
}